}

.swiper-slide {
  background-size: cover;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.swiper-button-container {
  background-color: rgba(0, 0, 0, .25);
}

.swiper-button-prev, .swiper-button-next {
  opacity: 1;
  background: none;
}

.swiper-button-next {
  right: 10px;
}

.swiper-button-prev {
  left: 0;
}

@media screen and (min-width: 1280px) {
  .swiper-button-prev {
    left: 13px;
  }
}

.swiper-slide:before {
  content: "";
  width: 100%;
  height: 100%;
  opacity: .4;
  position: absolute;
  top: 0;
  left: 0;
}

.swiper-slide {
  pointer-events: none;
  opacity: 0;
  justify-content: center;
  align-items: center;
  transition: transform .25s;
  display: flex;
  transform: scale(.95);
}

.swiper-slide-active {
  opacity: 1;
  transition: all 1s;
  transform: scale(1);
}

.swiper-img {
  max-width: 306px;
  height: 100%;
  width: 100%;
