h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

.link,
a {
  text-decoration: none;
  color: currentColor;
}

.list,
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

button {
  padding: 0;
  border: 0;
  background-color: transparent;
  cursor: pointer;
}

img {
  display: block;
  max-width: 100%;
  height: auto;

  object-fit: cover;
  object-position: center;
}
