/* */

.backdrop {
  z-index: 1;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}

.backdrop--is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: var(--animation);
  &:hover {
    opacity: 1;
    transition: var(--animation);
  }
}

.modal {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 320px;
  padding: 32px 20px;
  transform: translate(-50%, -50%);
  background: var(--bg-yellow-cl);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);
  border-radius: 30px;

  @media (min-width: 1280px) {
    width: 458px;
  }
}

.modal__btn {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 12px;
  right: 12px;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background-color: var(--bg-light-cl);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06);
  cursor: pointer;

  // &:hover,
  // &:focus {
  //     fill: $hover;
  // }
}

/* Form */

.modal__text {
  margin-bottom: 20px;
  text-align: center;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.15;
  // color: $title-text;
}

.modal__form {
  margin-right: auto;
  margin-left: auto;
}

.modal__item {
  &:not(:last-child) {
    margin-bottom: 10px;
  }

  &:last-child {
    margin-bottom: 20px;
  }
}

.modal__label {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  line-height: 1.16;
  letter-spacing: 0.01em;
  // color: $text;
}

.modal__line {
  position: relative;
  margin-top: 4px;
}

.modal__input {
  width: 100%;
  padding: 12px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  outline: transparent;
  resize: none;

  border: 1px solid var(--accent-color);
  box-shadow: inset 0px 2px 4px rgba(0, 0, 0, 0.15);
  border-radius: 30px;

  &:not(:last-child) {
    height: 40px;
    // padding-left: 42px;
  }
}

.modal__textarea {
  width: 100%;
  height: 120px;
  resize: none;
  padding: 12px 16px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  outline: transparent;
  cursor: pointer;

  &:focus {
    // border-color: $hover;
    cursor: text;
  }
}

.modal__line:focus-within .modal__input,
.modal__line:focus-within .modal__icon {
  // fill: $hover;
  // border-color: $hover;
  cursor: text;
}

.modal__icon {
  position: absolute;
  top: 50%;
  left: 15px;
  transform: translateY(-50%);
}

.modal__last-div {
  margin-right: auto;
  margin-left: auto;
}

.modal__last-label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 31px;
  @media (min-width: 200px) {
    display: flex;
    justify-content: space-evenly;
  }
}

.span-text {
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.03em;
  @media (min-width: 1200px) {
    font-size: 14px;
    line-height: 24px;
  }
}

.modal__checkbox {
  appearance: none;
  position: absolute;
}

.modal__span {
  width: 16px;
  height: 15px;
  border: 2px solid rgba(33, 33, 33, 1);
  border-radius: 2px;
}

.modal__link {
  text-decoration: underline;
  // color: $logo;
}

.modal-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-right: auto;
  margin-left: auto;
  padding: 10px 38px;

  font-weight: 500;
  font-size: 13px;
  line-height: 150%;

  border-radius: 4px;
  width: 106px;
  height: 40px;
  border: 2px solid var(--accent-color);
  border-radius: 30px;
  background-color: var(--bg-light-cl);
  cursor: pointer;

  &:hover,
  &:focus {
    background-color: var(--bg-accent-cl);
  }
}
